* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #333;
}

.nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    /* Positions items at opposite ends of the container */
    height: 5rem;
    align-items: center;
    /* Adjust if you want the items to stretch vertically */
    gap: 2rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
    /* This background color will be overridden by the previous one */
}

.site-title {
    font-size: 3rem;
    width: 228px;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 1.5rem;
}

.nav a {
    height: 100%;
    display: flex;
    align-items: center;
    color: inherit;
    text-decoration: none;
}

.nav li.active {
    background-color: #555;
}

.nav li:hover {
    background-color: #777;
}


.container {
    /* Set a minimum margin for the container */
    margin: 20px auto;
    /* Auto margin horizontally centers the container */
    max-width: 800px;
    /* Set a fixed width for the container */
}

@media (max-width: 850px) {
    .container {
        max-width: 600px;
        /* Reduce the width of the container to 80% of the window width */
    }
}


@media (max-width: 650px) {
    .container {
        max-width: 400px;
    }
}

.mainBody {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
}

.textBody {

    padding-bottom: 2rem;
    font-size: larger;
}


.form {
    display: flex;
    flex-direction: column;
}

.form-inputs {
    width: 100%;
    padding-right: 15px;
    padding-bottom: 15px;
}

.form-inputs p {
    color: red;
}

.name-forms {
    display: flex;
    flex-direction: row;
    width: 100%;
}

.name-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 10px;
    height: 40px;
    width: 100%;
    border: solid;
    border-color: white;
    border-width: thin;
    background-color: #333;
}

.email-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 10px;
    height: 40px;
    width: 100%;
    border: solid;
    border-color: white;
    border-width: thin;
    background-color: #333;

}



.form-content-right {
    width: 100%;


}

.form-input-btn {
    width: 40%;
    height: 50px;
    margin-top: 10px;
    border-radius: 10px;
    outline: none;
    border: none;
    color: white;
    font-size: 1rem;
}

.button_text {
    color: #333;
}

.success_message {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    font-size: 50px;
}